import React from 'react'
import DistrictComponent from './DistrictComponent'
import Card from 'react-bootstrap/Card'
import ListGroup from 'react-bootstrap/ListGroup'
import './App.css'
function StateComponent(props) {
    var stateCode = {
        AN: "Andaman and Nicobar Islands",
        AP: "Andhra Pradesh",
        AR: "Arunachal Pradesh",
        AS: "Assam",
        BR: "Bihar",
        CH: "Chandigarh",
        CT: "Chhattisgarh",
        DN: "Dadra and Nagar Haveli",
        DD: "Daman and Diu",
        DL: "Delhi",
        GA: "Goa",
        GJ: "Gujarat",
        HR: "Haryana",
        HP: "Himachal Pradesh",
        JK: "Jammu and Kashmir",
        JH: "Jharkhand",
        KA: "Karnataka",
        KL: "Kerala",
        LA: "Ladakh",
        LD: "Lakshadweep",
        MP: "Madhya Pradesh",
        MH: "Maharashtra",
        MN: "Manipur",
        ML: "Meghalaya",
        MZ: "Mizoram",
        NL: "Nagaland",
        OR: "Odisha",
        PY: "Puducherry",
        PB: "Punjab",
        RJ: "Rajasthan",
        SK: "Sikkim",
        TG: "Telengana",
        TN: "Tamil Nadu",
        TS: "Telangana",
        TR: "Tripura",
        UP: "Uttar Pradesh",
        UK: "Uttarakhand",
        UT: "Uttarakhand",
        WB: "West Bengal"
      };
    if(stateCode[props.data_name]){
        // console.log(typeof(stateCode[props.data_name]))
        return (
            <Card className="addPadding" border="light">
            <Card.Header>{props.data.total && stateCode[props.data_name]}</Card.Header>
            <ListGroup className="list-group-flush">
                <ListGroup.Item>Confirmed : {props.data.total && props.data.total.confirmed.toLocaleString('en-IN')}</ListGroup.Item>
                <ListGroup.Item>Deceased : {props.data.total && props.data.total.deceased.toLocaleString('en-IN')}</ListGroup.Item>
                <ListGroup.Item>Recovered : {props.data.total && props.data.total.recovered.toLocaleString('en-IN')}</ListGroup.Item>
                <ListGroup.Item>Tested : {props.data.total && props.data.total.tested.toLocaleString('en-IN')}</ListGroup.Item>
            </ListGroup>
            </Card>
        )
    }
    return null;

}

export default StateComponent
