import React from 'react'

function DistrictComponent(props) {
    // if() console.log(props.data.total.confirmed);
    return (
        <div class="districtClass">
            {props.data.total && <li>{props.data.total.confirmed}</li>}
            {props.data.total && <li>{props.data.total.deceased}</li>}
            {props.data.total && <li>{props.data.total.recovered}</li>}
            {props.data.total && <li>{props.data.total.deceased}</li>}
        </div>
    )
}

export default DistrictComponent
