import './App.css';
import { useState, useEffect} from 'react';
import axios from 'axios';
import StateComponent from './StateComponent'
import Row from 'react-bootstrap/Row'
import Container from 'react-bootstrap/Container'
import '../node_modules/bootstrap/dist/css/bootstrap.min.css';
import Navbar from 'react-bootstrap/Navbar'

function App() {
  const [data, setData] = useState([]);
  const [isLoaded, setIsLoaded] = useState(false);
  useEffect(()=>{
    axios
    .get('https://api.covid19india.org/v4/min/data.min.json')
    .then(res =>{
      setData(res.data);
      setIsLoaded(true);
    })
  }, [])
  return (
    <div>
      <Navbar bg="dark" variant="dark">
        <Navbar.Brand href="#home" className="mid-align">Covid-19 Tracker</Navbar.Brand>
      </Navbar>
      <Container className="addPadding">
        <Row xs={1} sm={2} md={3} lg={4}>
            {isLoaded && Object.keys(data).map(state_name =>
              <StateComponent key={state_name} data={data[state_name]} data_name={state_name}/>
            )}
        </Row>
      </Container>
    </div>
  );
}

export default App;
